@import url(https://fonts.googleapis.com/css2?family=Zen+Loop:ital@1&display=swap);

body {
  margin: 0;
  font-family:"" -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
margin:0px;
padding:0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App{
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  background: linear-gradient(to right,red,white);


}
.app_searchForm{

  margin-top: 18px ;
}
.app_input{
 padding :6px 16px;
 min-width:200px;
 max-width:100vw;
 font_size:15px;

}
.app_submit{
  padding: 6px 16px;;
  font-size: 15px;
  margin-left: 6px;
 background-color:violet;
 color:white;
 cursor:pointer;
}
.app_submit:hover{
  padding: 6px 16px;;
  font-size: 15px;
  margin-left: 6px;
 background-color:pink;
 color:red;
 cursor:pointer;
}
.app_rec{
  margin-top: 25px;
  display:grid;
 grid-template-columns:230px 230px;
 align-items:baseline;


}
.app_healthlabel{

  padding: 6px 9px;;
  border-radius: 4px;
  margin-left: 6px;
}

.rt_img{
    height:200px;
    width:200 px;
   object-fit:cover;
   border-radius:4px;
}
.rt_name{
    max_width:200px;
    text_align:center;
    margin:12px 0px;
}
.RecipeTile{
    cursor:pointer;
}
